    &__navigation {
      ul {
        &>li {
          padding: 0 3px;
          &>a, &>span {} } } }
    &__logo {
      .span {
        width: 250px; } } }

  .header {
    &__navigation {
      ul {
        &>li {
          padding: 0 5px; } } }
    &__right {
      &>div {
        &:last-child {
          width: 50px; } } }
    &__wrapper {
      &>div {
        &.first {
          width: 180px; } } } }

  .news {
    &__img {
      height: 212px; } }

  .special-offers {
    &__block {
      &>div {
        &.first {
          width: 213px; } } } }

  .table-basic {
    font-size: rem(12);
    &>tbody, &>tfoot, &>thead {
      &>tr {
        &>td, &>th, {
          padding-left: 8px;
          padding-right: 8px; } } } }

  .user {
    &__product {
      &>div {
        &:first-child {
          width: 375px; } } } }

  .hay {
    &__block {
      width: 227px;
      height: 295px; }
    &__strong {
      font-size: rem(85);
      left: 21px;
 }      // bottom: 28px
    &__text {

      &::after {
        left: 158px; } } }

  .auto {
    &__block {
      margin-right: 65px;
      width: 286px;
      height: 382px; }
    &__car {
      &__border {
        width: 286px;
        height: 382px; }
      &__img {
        width: 348px; } }
    &__text {
      font-size: rem(67);
      margin-right: 20px;
      margin-top: 55px;
      strong {
        font-size: rem(47); } }
    &__background {
      width: 163px;
      top: 46px; } }

  .btn {
    &--auto-register {
      height: 54px;
      line-height: 54px;
      width: 407px;
      bottom: 63px;
      margin-right: -70px; } }

  .chat {
    &__block, &__date {
      margin-bottom: 25px; } }

  .order {
    &__form {
      &__documents {
        .btn {
          width: 140px;
          &--blue {
            float: left; } } } } }

  #client {
    .slider {
      width: 720px; } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
