    font-size: rem(30); }

  .footer {
    &__navigation {
      display: none; } }

  .header {
    &__logo {
      img {
        width: 12px;
        background-position-y: 2px; } }

    &__wrapper {
      &>div {
        &.first {
          width: auto; } } } }

  .body__main {
    .header__wrapper > div.first {
      width: auto; } }

  .content {
    &__head {
      &--register {
        text-align: center; } } }

  .news {
    &__img {
      height: 157px; } }

  .special-offers {
    &__block {
      &>div {
        &.first {
          width: 157px; } } } }

  .navigation-level2 {
    &--user {
      ul {
        li {
          .label {
            right: 0;
            top: 8px; } } } } }

  .user {
    &__head {
      margin-bottom: 10px;
      .h1, h1 {
        display: block;
        margin-bottom: 5px;
        &+.user__head__label {
          margin-left: 0; } } }

    &__product {
      &>div {
        &:first-child {
          width: 220px; } } } }

  .hay {
    &__block {
      width: 100%;
      height: 198px;
      margin-bottom: 44px; }
    &__strong {
      font-size: rem(85);
      left: inherit;
      right: 38px;
      bottom: inherit;
      top: 50%;
      transform: translateY(-50%); }
    &__text {
      &::after {
        display: none; } } }

  .auto {
    &__block {
      margin-top: 0; } }

  .content {
    &--main {
      padding-bottom: 65px; } }

  .slideout-panel {
    box-shadow: 0 0 10px rgba(0,0,0,.2); }

  .table-basic {
    &>tbody, &>tfoot, &>thead {
      &>tr {
        &>td, &>th, {
          padding-left: 5px;
          padding-right: 5px; } } } }

  .content {
    &__head {
      &--arrow {
        h1, .h1 {
          &::after {
            top: 8px; } } } } }

  .order {
    &__form {
      &__documents {
