          width: 95px;
          padding-left: 0;
          padding-right: 0;
          min-width: inherit; } } } }

  .form--order {
    padding: 0;
    border: 0; }

  .form__block__delivery {
    width: 53%;
    float: right;
    margin-top: -177px; } }

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {

  .footer {
    &__wrapper {
      padding: 30px 0;
      &>div.first {
        width: auto; } }
    &__logo {
      margin: 0 auto;
      .copyright {
        display: block; }
      &>div {
        vertical-align: top; }
      .span {
        width: 280px; } } }

  .copyright {
    white-space: normal;
    float: none;
    margin-top: 10px; }

  .header {
    &__navigation {
      display: none;
      position: absolute;
      z-index: 1000;
      width: 100%;
      top: 33px;
      padding-top: 10px;
      padding-bottom: 10px;
      animation: visible 0.3s linear;
      background: rgba($color-white, 0.9);
      ul {
        &>li {
          display: block;
          &>a, &>span {
            display: block;
            height: 50px;
            line-height: 50px;
            &::after {
              display: none; }
            &::before {
              transform: rotate(0) translateX(-50%);
              left: 50%;
              bottom: 0;
              top: inherit; } }
          &:last-child {
            &>a, &>span {
              &::before {
                display: none; } } } } } }
    &__button {
      display: block;
      margin: 0 auto; }
    &__menu {
      position: relative;
      max-width: 300px;
      text-align: center;
      margin: 0 auto;
      &.is-open {
        .header__navigation {
          display: block; } } }

    &__wrapper {
      &>div {
        &.last {
          width: 165px; } } } }

  .navigation-level2 {
    border-bottom: 1px solid $color-gray; }

  .form {
    &__row__group--xs-block {
      display: block;
      &>div {
        display: block;
        &+div {
          padding-top: 5px; } } }
    &-control, &__control {
      &--chat {
        width: 140px; } } }

  .news {
    &__img {
      height: 210px; } }

  .special-offers {
    &__block {
      &>div {
        &.first {
          display: none; }
        &+div {
          padding-left: 0 !important; } } } }

  .user {
    &__menu {
      display: block; }

    &__product {
      &>div {
        width: 100%;
        display: block;
        &:first-child {
          width: 100%;
          padding-bottom: 40px; }
        &+div {
          padding-left: 0 !important; } }
      &__img {
        display: block;
        max-width: 500px;
        margin: 0 auto; } } }

  .navigation-level2 {
    border-bottom: 0;
    padding-bottom: 0;
    ul {
      li {
        border-bottom: 1px solid $color-gray;
        a {
          padding-top: 12px;
          padding-bottom: 12px; }
        .label {
          top: 13px; }
        &.active {
          &::before {
            top: 50%; } } } } }

  .mobile-logo {
    position: absolute;
    bottom: 10px;
    text-align: center;
    width: 260px; }

  .hay {
    &__block {
      width: 100%;
      height: 212px;
      // margin-bottom: 105px
      float: none; }
    &__strong {
      left: 40px;
      right: inherit;
      transform: none;
      top: inherit;
      bottom: -62px;
      z-index: 100;
      &::after {
        content: '';
        position: absolute;
        height: 6px;
        top: 50%;
        margin-top: -4px;
        background-color: $color-white;
        width: 100%;
        z-index: -1;
        left: 0; } } }

  .content {
    &--main {
      padding-top: 0; } }

  .auto {
    &__block {
      width: 250px;
      height: 333px;
      margin: 250px auto 0;
      float: none;
      right: -41px; }
    &__car {
      &__border {
        width: 250px;
        height: 333px; }
      &__img {
        width: 303px; } }
    &__background {
      width: 142px;
      top: 40px; }
    &__text {
      margin-top: -140px;
      margin-right: -288px;
      strong {
        br {
          display: none; } } } }

  .btn {
    &--auto-register {
      margin-right: -290px;
      bottom: 30px; } }

  .chat {
    &__row {
      &>div {
        &+div {
          padding-left: 15px !important; } } }
    &__search {
      margin-right: 0;
      &.is-active {
        width: 170px; } } }

  .popup {
    &__product {
      &>div:first-child {
        width: 170px; } } }

  .order {
    &__form {
      &__btn {
        text-align: center; }

      &__documents {
        .btn {
          margin-top: 15px;
          width: 214px;
          &--border {
            float: right;
            width: 160px; } } } } }

  .pagination--news {
    margin-bottom: 25px; }

  .form__block__delivery {
    width: 100%;
    float: none;
    margin-top: 0; }

  #client {
    .slider {
      width: 440px;
      .viewport {
        width: 350px;
        .items_container .item {
          width: 350px; } }
      .slider_navigation .dots_container {
        width: 350px; } } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {

  .footer {
    &__logo {
      &>div {
        &:first-child {
          display: none; }
        &+div {
          padding-left: 0; } } } }

  .header {
    &__wrapper {
      display: block;
      &>div {
        display: block;
        width: 100%;
        &.first {
          display: none; }
        &.last {
          width: 100%; } } }
    &__right {
      float: none;
      width: 100%; }
    &__enter {
      &__button {
        text-align: left;
        // padding-left: 1px
        padding-right: 0;
        .btn {
          display: block; } }
      &__body {
        left: -1px;
        right: -2px;
        // width: 100%
        min-width: 290px; }
      &__wrapper {
        width: 100%;
        padding-bottom: 20px; }
      &__bottom {
        display: block;
        &>div {
          display: block; }
        .btn {
          display: block;
          margin-bottom: 10px;
          margin-top: 4px;
          width: 100%; } }
      &__link {
        text-align: center; } }
    &__button {
      width: 100%;
      margin-bottom: 20px; } }

  .col-xxs-12 {
    width: 100% !important; }

  .table {
    &--xxs-block {
      display: block !important;
      .table__cell {
        display: block !important;
        &+.table__cell {
          padding-left: 0;
          padding-top: 8px; } } } }

  .btn {
    &--xxs-block {
      display: block !important;
      width: 100% !important; } }

  .h1 {
    &--afLine {
      &::after {
        display: none; } } }

  .navigation-level2 {
    &--help {
      border-bottom: 0; } }

  .form {
    &__row__group--xxs-block {
      display: block;
      &>div {
        display: block;
        &+div {
          padding-top: 5px; } } }
    &__group {
      &--captcha {
        display: block;
        &>div {
          display: block;
          float: left;
          &.first {
            width: calc(100% - 130px); }

          &.first {
            label {
              padding-top: 12px;
              display: block; } }
          &.center {
            overflow: hidden;
            width: 130px;
            border-left: 1px solid $color-gray;
            border-right: 1px solid $color-gray;
            .captcha {
              position: relative;
              left: 50%;
              transform: translateX(-50%); } }
          &.last {
            width: 100%;
            margin-top: -1px; } } }

      &--chat {
        display: block;
        border-top: 1px solid $color-gray;
        padding-top: 40px;
        overflow: hidden;
        &>div {
          display: block;
          &:first-child {
            width: calc(100% - 60px);
            float: left; }
          &:nth-child(2) {
            float: left; }
          &:last-child {
            padding-top: 40px;
            float: left;
            width: 100%; }
          &.center {
            border-top: 0;
            height: 40px; } }
        .form-control {
          border-top: 0; } } }

    &-control, &__control {
      &--chat {
        width: calc(100% - 25px); } } }

  .strong--frh480 {
    display: block;
    margin-top: 5px; }

  .btn {
    &--biggest {
      font-size: rem(15); } }

  .tag {
    margin-bottom: 40px; }

  .news {
    &__img {
      height: 280px; }

    &__calendar__month + .news__calendar__month {
      margin-top: 10px; } }

  .link-history {
    margin-right: -95px; }

  .hay {
    &__block {
      height: auto;
      margin-bottom: 100px;
      padding-top: 65px;
      padding-bottom: 80px; }
    &__text {
      position: relative;
      width: calc(100% - 85px);
      top: 0; } }

  .auto {
    &__block {
      width: 235px;
      height: 313px;
      margin: 285px auto 0 0;
      right: 0; }
    &__car {
      &__border {
        width: 235px;
        height: 313px; }
      &__img {
        width: 284px; } }
    &__background {
      display: none; }
    &__text {
      margin-top: -190px;
      margin-right: -290px;
      strong {
        br {
          display: block; } } } }

  .btn {
    &--auto-register {
      margin-right: -290px;
      bottom: 35px;
      width: 290px;
      font-size: rem(13); }

    &--xxs-block {
      &+.btn--xxs-block {
        margin-left: 0;
        margin-top: 15px; } } }

  .chat {
    &__row {
      display: block;
      overflow: hidden;
      &>div {
        display: block !important;
        &+div {
          padding-left: 0 !important;
          margin-top: 15px; } } }
    &__search {
      float: none;
      &.is-active {
        width: 100%; } } }

  .hidden-xxs {
    display: none !important; }

  .popup {
    &__product {
      display: block;
      &>div {
        display: block !important;
        &:first-child {
          margin: 0 auto; }
        &+div {
          padding-left: 0 !important;
          padding-top: 10px; } } } }

  .visible-xxs {
    display: block !important; }
  tr.visible-xxs {
    display: table-row !important; }
  td.visible-xxs {
    display: table-cell !important; }

  .table-basic {
    &--total {
      &>tbody {
        &>tr {
          &>td {
            display: block;
            width: 100%;
            &:last-child {
              padding-left: 0 !important;
              padding-right: 0 !important; } } } } } }

  .table-basic--total .total {
    text-align: center; }

  .pagination ul li {
    margin: 0 2px; }

  #client {
    .slider {
      width: 290px;
      .viewport {
        width: 290px;
        .items_container .item {
          width: 290px; } }
      .slider_navigation {
        .dots_container {
          width: 290px; }
        .nav_container {
          // position: absolute
          // width: 80px
          top: -45px;
          z-index: 1000;
          // left: 50%
 } } } } }          // transform: translateX(-50%)

