.navigation-level2 {
  font-size: rem(14);
  margin-bottom: 20px;
  padding-bottom: 20px;
  font-family: $font-roboto-medium;
  ul {
    margin: 0;
    padding: 0;
    li {
      margin: 0;
      padding-left: 35px;
      position: relative;
      list-style: none;
      display: block;
      &::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 1px;
        background: $color-blue;
        left: 0;
        top: 12px;
        display: none; }
      &>a, &>span {
        color: $primary-color;
        display: block;
        padding: 4px 0;
        text-decoration: none;
        position: relative;
        &:hover {
          color: $color-blue; } }
      &.active {
        &>a, &>span {
          color: $color-blue; }
        &::before {
          display: block; } }
      .label {
        display: inline-block;
        color: $color-white;
        background: $color-blue;
        width: 20px;
        height: 20px;
        line-height: 21px;
        text-align: center;
        font-size: rem(10);
        font-family: $font-roboto-light;
        margin-left: 10px;
        vertical-align: middle;
        position: absolute; } } }

  &--border {
    border-bottom: 1px solid #e8e8e8; }

  &--user {
    ul {
      li {
        &>a, &>span {
          padding-right: 35px; } } } } }

/**/
.list {
  font-size: rem(14);
  li {
    line-height: 1.85; }
  &--mark {
    margin-left: 0;
    padding-left: 0;
    li {
      list-style: none;
      margin-left: 0;
      position: relative;
      padding-left: 30px; } } }

ul.list--mark {
  li {
    &::before {
      content: "\f412";
      position: absolute;
      font-family: "Ionicons";
      left: 0;
      top: 3px;
      font-size: rem(22);
      color: $color-blue;
      display: block;
      line-height: 1; } } }

ol.list--mark {
  counter-reset: list;
  li {
    &::before {
      counter-increment: list;
      content: counter(list);
      display: block;
      height: 20px;
      line-height: 21px;
      width: 20px;
      border-radius: 50%;
      background: $color-blue;
      color: $color-white;
      font-size: rem(10);
      position: absolute;
      left: 0;
      top: 2px;
      text-align: center; } } }
