/* carousel */

#client {
  position: relative;
  ul {
    margin: 0;
    padding: 0;
    li {
      margin: 0; } }
  h2 {
    padding-top: 0; }
  .slider {
    width: 745px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 30px;
    padding-bottom: 0;
    .viewport {
      position: relative;
      width: 555px;
      height: 235px;
      margin-left: auto;
      margin-right: auto;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: 40px;
        top: 0;
        background: #fff;
        z-index: 2; }
      &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 40px;
        top: 0;
        background: #fff;
        z-index: 2;
        left: 0;
        background: linear-gradient(90deg, #fff 0, hsla(0, 0%, 100%, 0)); }
      &:after {
        right: 0;
        background: linear-gradient(270deg, #fff 0, hsla(0, 0%, 100%, 0)); }
      .items_container {
        width: 555px;
        position: relative;
        transition: all 0.8s ease-in-out;
        .item {
          float: left;
          transition: all .8s ease-in-out;
          line-height: 235px;
          width: 555px;
          img {
            transition: all .8s ease-in-out;
            transform: scale(0.6);
            opacity: .6;
            max-width: 235px; }
          &.active img {
            transform: scale(1);
            opacity: 1; } } } }
    .slider_navigation {
      position: relative;
      width: 100%;
      height: 40px;
      margin: 39px auto 0;
      .dots_container {
        width: 555px;
        height: 40px;
        margin: 0 auto;
        line-height: 40px;
        border-top: 1px solid #acacac;
        .dot {
          display: inline-block;
          position: relative;
          width: 20%;
          height: 1px;
          padding: 10px 0;
          float: left;
          cursor: pointer;
          &:before {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 1px;
            top: 0;
            left: 0;
 }            // background: #acacac
          span {
            position: relative;
            top: 0px;
            color: #adacb1;
            opacity: 0;
            transition: all .8s ease-in-out;
            font-weight: 400;
            font-style: normal;
            font-size: rem(20);
            font-family: $font-intro;
            display: inline-block;
            padding: 0 1px;
            &:before {
              content: "0"; } }
          .highlight {
            content: '';
            position: absolute;
            width: 0;
            height: 3px;
            top: -1px;
            left: 0;
            right: 0;
            margin: 0 auto;
            background: $color-blue;
            transition: all 0.8s ease-in-out; }
          &:hover {
            .highlight {
              width: 100%;
              transition: all 0.4s ease-in-out; }
            span {
              top: -10px;
              opacity: 1;
              transition: all 0.4s ease-in-out; } }
          &.active {
            span {
              top: -28px;
              background: $color-white;
              color: $color-blue;
              opacity: 1;
              font-weight: 400;
              font-style: normal;
              font-size: 20px; }
            .highlight {
              width: 100%; } }
          &.dir_left .highlight {
            right: auto;
            left: 0; }
          &.dir_right .highlight {
            left: auto;
            right: 0; } } }
      .nav_container {
        position: absolute;
        width: 100%;
        top: -16px;
        .nav {
          width: 30px;
          height: 30px;
          cursor: pointer;
          position: relative;
          border: 1px solid $color-blue;
          color: $color-white;
          background: $color-blue;
          transition: all 0.3s linear;
          font-size: rem(14);
          &::after {
            line-height: 0;
            font-family: $font-ionicons;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%); }
          &:hover {
            background: $color-white;
            color: $color-blue; }
          &.left {
            float: left;
            &::after {
              content: "\f124"; } }
          &.right {
            float: right;
            &::after {
              line-height: 0;
              content: "\f125"; } } } } } }
  .text_box {
    position: relative;
    height: 100px;
    text-align: center;
    // margin-top: 15px
    color: #636363;
    .item {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
      height: 70px;
      z-index: 1;
      // animation: i .4s ease-in-out
      // animation-fill-mode: both
      p {
        font-size: 18px;
        line-height: 28px;
        margin-bottom: 10px;
        transition: all 0.4s ease-in-out;
        font-family: PT Sans, sans-serif;
        font-weight: 400;
        font-style: normal;
        opacity: 0;
        &.link {
          font-family: PT Sans, sans-serif;
          font-weight: 400;
          font-style: normal;
          opacity: 0;
          color: #adacb1;
          line-height: 20px;
          font-size: 14px; }
        a {
          text-decoration: underline; } }
      &.active {
        z-index: 2;
        // animation: h .4s ease-in-out
        // animation-fill-mode: both
        // margin-top: 60px
        transition-delay: 0.4s;
        a, p {
          opacity: 1;
          transition-delay: 0.4s; }
        a {
          visibility: visible; } } } } }

